import React from "react"
import { Button, Layout, Link } from "../components/"
import { Github, LinkedIn, Youtube, Twitter } from '../assets/index'
import * as styles from '.././styles/index.module.css'

const IndexPage = ({location}) => {
  return(
    <Layout>
        <section className={styles.container}>
          <div className="md:w-3/5 w-full space-y-3">
            <h1 className={styles.tag}>Javascript Developer</h1>
            <h3 className="">Hello!, I'm Caner Tuzunar,  Curious Software Developer</h3>
            <Button>DOWNLOAD CV</Button>
          </div>
          <div className={styles.socialSide}>
            <Link external to="https://www.github.com/canertuzunar"><Github /></Link>
            <Link external to="https://www.linkedin.com/in/caner-tuzunar-800882109/"><LinkedIn /></Link>
            <Link external to="https://www.youtube.com/channel/UCACz1miqCSwPsFH3P1tQ7gQ"><Youtube/></Link>
            <Link external to="https://twitter.com/tuzunar"><Twitter /></Link>
          </div>
        </section>
    </Layout>
  )
}

export default IndexPage